// Require Cordova plugin : cordova-plugin-camera
<template>
  <v-container text-center>
    <v-row dense v-show="this.especes == null">
      <v-text
        >Pas de connexion internet, impossible de charger les fiches espèces
        !</v-text
      >
      <v-btn @click="getData()">Reload Page</v-btn>
    </v-row>
    <v-form ref="form" v-model="form" class="pa-4 pt-6">
      <v-autocomplete
        v-model="select"
        :items="especes"
        :rules="[(v) => !!v || $t('report.form.speciesWarning')]"
        outlined
        color="indigo"
        style="min-height: 96px"
        :label="$t('report.form.speciesField')"
        prepend-icon="pets"
        required
      ></v-autocomplete>

      <v-textarea
        v-model="description"
        auto-grow
        outlined
        color="indigo"
        :label="$t('report.form.descField')"
        style="min-height: 96px"
        rows="1"
        prepend-icon="description"
      ></v-textarea>

      <v-text-field
        v-model="imagePath"
        outlined
        color="indigo"
        :label="$t('report.form.picField')"
        style="min-height: 96px"
        :rules="[(v) => !!v || $t('report.form.picWarning')]"
        prepend-icon="camera_alt"
      ></v-text-field>

      <img v-if="imagePath !== ''" :src="imagePath" />
      <v-btn color="indigo" class="ma-2 white--text" @click="takePicture">
        <v-icon left>add_a_photo</v-icon
        >{{ $t("report.form.takePhotoButton") }}</v-btn
      >
    </v-form>
    <v-divider></v-divider>
    <v-card-actions>
      <v-btn
        :disabled="!form"
        :loading="isLoading"
        class="white--text"
        color="success"
        depressed
        @click="sendForm"
        >{{ $t("report.form.sendButton") }}</v-btn
      >
      <v-spacer></v-spacer>
      <v-btn text @click="$refs.form.reset()" color="error">
        {{ $t("report.form.resetButton") }}
      </v-btn>
    </v-card-actions>
  </v-container>
</template>

<script>
const axios = require("axios");
import { nativeAlert } from "../libs/index";

export default {
  name: "report",
  data() {
    return {
      especes: null,
      select: null,
      description: "",
      imagePath: "",

      form: false,
      isLoading: false,
    };
  },
  beforeMount() {
    this.getData()
  },
  created() {
    this.especes = JSON.parse(localStorage.getItem("especesDB"));
  },
  mounted() {
    if (this.$route.params.ficheName != null) {
      this.select = this.$route.params.ficheName;
    }
  },
  methods: {
    getData(){
      axios
      .get(
        "https://gaia.oec.fr/getdata.php?do=get_especes_inv&key=fea9a667df9db40499ebf94e5b6a07f6"
      )
      .then((response) =>
        localStorage.setItem(
          "especesDB",
          JSON.stringify(response.data.result.data.map(especes => especes.nom_scientifique))
        )
      );
      this.especes = JSON.parse(localStorage.getItem("especesDB"));
    },
    takePicture() {
      if (navigator.camera) {
        navigator.camera.getPicture(this.setPicture, this.error, {});
      } else {
        this.error();
      }
    },
    setPicture(imagePath) {
      this.imagePath = imagePath;
    },
    error() {
      nativeAlert(this.$t("error"));
    },
    locError() {
      nativeAlert(
        this.$t("report.pos.positionNotAvailable"),
        this.$t("report.pos.title")
      );
    },
    validate() {
      this.$refs.form.validate();
    },
    reset() {
      this.imagePath = "";
      this.$refs.form.reset();
    },
    sendForm() {
      if (navigator.geolocation) {
        navigator.geolocation.getCurrentPosition(
          this.successLoc,
          this.locError,
          {
            enableHighAccuracy: true,
            timeout: 3000,
          }
        );
      } else {
        this.locError();
      }
    },
    successLoc(pos) {
      nativeAlert(
        "Espèce : " +
          this.select +
          "\nDescription : " +
          this.description +
          "\nPhoto : " +
          this.imagePath +
          "\nLatitude : " +
          pos.coords.latitude +
          "\nLongitude : " +
          pos.coords.longitude,
        this.$t("report.pos.title")
      );
    },
  },
};
</script>

<style scoped>
img {
  width: 50%;
  display: block;
  margin: auto;
}
</style>
