// Require Cordova plugin :

<template>
  <v-container>
    <v-row dense v-show="this.articles == null">
      <v-text
        >Pas de connexion internet, impossible de charger les fiches espèces
        !</v-text
      >
      <v-btn @click="getData()">Reload Page</v-btn>
    </v-row>
    <v-flex>
      <div v-for="article in articles" :key="article.id">
        <v-card class="mb-6 mt-2">
          <v-img v-if="!article.image" height="200px" v-bind:src="'aliemLogo.jpg'"></v-img>
          <v-img v-if="article.image" height="200px" v-bind:src="article.image"></v-img>
          <v-card-title>
            <span>{{ article.title }}</span>
          </v-card-title>
          <v-card-text>
            {{ article.description | truncate(130)}}
          </v-card-text>
          <v-card-actions>
            <v-chip small color="secondary" class="white--text">
              {{ formatDate(article.date) }}
            </v-chip>
            <v-spacer></v-spacer>

            <v-btn
              small
              replace
              color="indigo"
              class="white--text"
              v-bind:href="article.url"
              >Voir plus</v-btn
            >
          </v-card-actions>
        </v-card>
      </div>
    </v-flex>
  </v-container>
</template>

<script>
const axios = require("axios");

export default {
  name: "news",
  data() {
    return {
      articles: null,
    };
  },
  beforeMount() {
    this.getData()
  },
  created() {
    this.articles = JSON.parse(localStorage.getItem("articlesDB"));
  },  
  methods: {
    getData(){
      axios
      .get(
        "http://aliem.arobase.corsica/json_cal_news.php"
      )
      .then((response) =>
        localStorage.setItem(
          "articlesDB",
          JSON.stringify(response.data.news)
        )
      );
      this.articles = JSON.parse(localStorage.getItem("articlesDB"));
    },
    formatDate(timestamp){
      let milliseconds = timestamp * 1000
      let dateObject = new Date(milliseconds)
      let humanDateFormat = dateObject.toLocaleDateString('fr-FR', {year: 'numeric', month: 'short', day: 'numeric'})
      return humanDateFormat
    }
  },
};
</script>
